<template>
  <main class="category">
    <h1 class="category__heading">{{ category }}</h1>
    <section class="category__photos">
      <div class="category__photo" v-for="image in images" v-bind:key="image.index">
        <img :src="`/img/photography/${category}/${image.file}`" alt="alt" />
      </div>
    </section>
  </main>
</template>

<script>
export default {
  name: 'CategoryPage',
  props: ['category'],
  data () {
    return {
      images: []
    }
  },
  mounted () {
    fetch(`/categories/${this.category}.json`)
      .then((response) =>
        response.json()
          // eslint-disable-next-line no-console
          .catch(err => { console.error(`'${err}' happened!`); return {} }))
      .then((json) => {
        this.images = json
      })
  }
}
</script>
